MemberList.propTypes = {
  getStoporders: PropTypes.func.isRequired,
  deleteMember: PropTypes.func.isRequired,

  errors: PropTypes.object.isRequired,
};

const mapStateToProps = (state) => ({
  member: state.member,
  security: state.security,
  errors: state.errors,
});

export default connect(mapStateToProps, { getStoporders, deleteMember })(
  MemberList
);
