import axios from "axios";
import { GET_ERRORS,GET_INSTITUTIONS, GET_INSTITUTION,GET_MEMBERINSTITUTION, DELETE_INSTITUTION } from "./types";

export const createInstitution = (institution, history) => async dispatch => {
  try {
    await axios.post("/api/institutions", institution);
    history.push("/institution-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateInstitution = (institution, history) => async dispatch => {
  try {
    await axios.put(`/api/institutions/${institution.id}`, institution);
    history.push("/institution-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getInstitutions = () => async dispatch => {
  const res = await axios.get("/api/institutions");
  dispatch({
    type: GET_INSTITUTIONS,
    payload: res.data
    
  });
  dispatch({
    type: GET_ERRORS,
    payload: {}
  });
  
};

export const getInstitution = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/institutions/${id}`);
    
    dispatch({
      type: GET_INSTITUTION,
      payload: res.data
    });
  } catch (error) {
    
    history.push("/home");
  }
};


export const getMembers = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/institutions/members/${id}`);
    
    dispatch({
      type: GET_MEMBERINSTITUTION,
      payload: res.data
    });
  } catch (error) {
    
    history.push("/home");
  }
};


export const deleteInstitution = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the institution and all the data related to it"
    )
  ) {
    await axios.delete(`/api/institutions/${id}`);
    dispatch({
      type: DELETE_INSTITUTION,
      payload: id
    });
  }
};


export const downloadSO =(institutionId,startDate,endDate)=>{
  try{
   
   axios(`/api/download-so-schedule/${institutionId}/${startDate}/${endDate}`, {
   method: 'GET',
   responseType: 'blob' //Force to receive data in a Blob Format
 })
 .then(response => {
 //Create a Blob from the PDF Stream
   const file = new Blob(
     [response.data], 
     {type: 'application/pdf'});
 //Build a URL from the file
   const fileURL = URL.createObjectURL(file);
 //Open the URL on new Window
 
   window.open(fileURL);
 })
}catch (err) {
 console.log(err.payload)
}
};


