import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { updateGrade, getGrade } from "../../actions/gradeActions";
import { getSectors } from "../../actions/sectorActions";

import classnames from "classnames";
import Select from "react-select";

class EditGrade extends Component {

  constructor() {
    super();

    this.state = {
      id: "",
      gradeName: "",
      gradeCode: "",
      weeklyWage: "",
      fortnightlyWage: "",
      basicWage: '',
      currency:'',
      sector: '',
      errors: {}
    };

    this.updateGrade = this.updateGrade.bind(this);
    this.cancel = this.cancel.bind(this);
    this.handleSectorChange = this.handleSectorChange.bind(this);
    this.handleCurrencyChange = this.handleCurrencyChange.bind(this);
  }

  componentDidMount() {

    const { gradeId } = this.props.match.params;
    this.props.getGrade(gradeId, this.props.history);
    this.props.getSectors();

  }

  //life cycle hooks
  componentWillReceiveProps(nextProps) {

    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }


    const {
      id,
      gradeName,
      gradeCode,
      weeklyWage,
      fortnightlyWage,
      basicWage,
      currency,
      sector
    } = nextProps.grade;

    this.setState({
      id,
      gradeName,
      gradeCode,
      weeklyWage,
      fortnightlyWage,
      basicWage,
      currency,
      sector
    });
  }

  onChange = e =>
    this.setState({
      [e.target.name]: e.target.value
    });

  handleSectorChange(val) {
    this.setState({ sector: val });
  }

  handleCurrencyChange(val) {
    this.setState({ currency: val.value });
  }
  updateGrade = e => {

    e.preventDefault();

    let grade = {
      id: this.state.id,
      gradeName: this.state.gradeName,
      gradeCode: this.state.gradeCode,
      weeklyWage: this.state.weeklyWage,
      fortnightlyWage: this.state.fortnightlyWage,
      basicWage: this.state.basicWage,
      currency:this.state.currency,
      sector: this.state.sector
    };

    this.props.updateGrade(grade, this.props.history);
  };

  cancel() {
    this.props.history.push("/grade-list");
  }


  render() {

    const currencies=[{value:"ZWL",label:"ZWL"},{value:"USD",label:"USD"}]
    const { sectors } = this.props.sector;
    const { errors } = this.state;

    return (
      <div className="container">

        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">Edit Grade</h5>
          <p className="lead text-center">Alter selected Grade Details</p>
          <hr />
          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.updateGrade}>
            <div className="row">
              <div className="form-group col-sm-6">
                <label >Grade Code</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.gradeCode })}
                  type="text"
                  name="gradeCode"
                  id="gradeCode"
                  value={this.state.gradeCode}
                  onChange={this.onChange}
                  placeholder="Grade Code"

                />
                {errors.gradeCode && (
                  <div className="invalid-feedback">
                    {errors.gradeCode}
                  </div>
                )}
              </div>
              <div className="form-group col-sm-6">
                <label >Grade Name</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.gradeName })}
                  type="text"
                  name="gradeName"
                  id="gradeName"
                  value={this.state.gradeName}
                  onChange={this.onChange}
                  placeholder="Grade Name"

                />
                {errors.gradeName && (
                  <div className="invalid-feedback">
                    {errors.gradeName}
                  </div>
                )}
              </div>




              <div className="form-group col-sm-6">
                <label >Weekly Wage</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.weeklyWage })}
                  type="text"
                  name="weeklyWage"
                  id="weeklyWage"
                  value={this.state.weeklyWage}
                  onChange={this.onChange}
                  placeholder="Weekly Wage"

                />
                {errors.weeklyWage && (
                  <div className="invalid-feedback">
                    {errors.weeklyWage}
                  </div>
                )}
              </div>
              <div className="form-group col-sm-6">
                <label >Fortnightly Wage</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.fortnightlyWage })}
                  type="text"
                  name="fortnightlyWage"
                  id="fortnightlyWage"
                  value={this.state.fortnightlyWage}
                  onChange={this.onChange}
                  placeholder="Fortnightly Wage"

                />
                {errors.fortnightlyWage && (
                  <div className="invalid-feedback">
                    {errors.fortnightlyWage}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Basic Wage</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.basicWage })}
                  type="text"
                  name="basicWage"
                  id="basicWage"
                  value={this.state.basicWage}
                  onChange={this.onChange}
                  placeholder="Basic Wage"

                />
                {errors.basicWage && (
                  <div className="invalid-feedback">
                    {errors.basicWage}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label>Sector</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleSectorChange}
                  getOptionLabel={sector => `${sector.sectorName}`}
                  isOptionSelected={true}
                  value={this.state.sector}
                  options={sectors}
                />
              </div>
              <div className="form-group col-sm-6">
                <label>Currency</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleCurrencyChange}
                  value={this.state.currency}
                  options={currencies}
                  value={{label : this.state.currency}}
                  required
                />
              </div>
            </div>
            <div className="row">
              <div className="form-group col-sm-6">
                <button
                  className="btn btn-primary btn-block"
                  type="submit">Save</button>
              </div>
              <div className="form-group col-sm-6">
                <button className="btn btn-secondary btn-block" onClick={this.cancel}>Cancel</button>
              </div>
            </div>
          </form>
        </div>
      </div>

    );
  }
}

EditGrade.propTypes = {
  updateGrade: PropTypes.func.isRequired,
  getSectors: PropTypes.func.isRequired,
  getGrade: PropTypes.func.isRequired,
  grade: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  sector: state.sector,
  grade: state.grade.grade,
  errors: state.errors
});

export default connect(mapStateToProps, {
  updateGrade,
  getSectors,
  getGrade
})(EditGrade);
