import { GET_MEMBERS,GET_STOPORDERS, GET_MEMBER, DELETE_MEMBER } from "../actions/types";

const initialState = {
  members: [],
  stoporders: [],
  member: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_MEMBERS:
      
      return {
        ...state,
        members: action.payload
      };

      case GET_STOPORDERS:
      
      return {
        ...state,
        stoporders: action.payload
      };

    case GET_MEMBER:
     
      return {
        ...state,
        member: action.payload
      };

    case DELETE_MEMBER:
      return {
        ...state,
        members: state.members.filter(
          member => member.id !== action.payload
        )
      };
    default:
      return state;
  }
}