import {GET_RECEIPTS,GET_RECEIPT,DELETE_RECEIPT} from "../actions/types";



const initialState = {
  receipts: [],
  receipt: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_RECEIPTS:
      
      return {
        ...state,
        receipts: action.payload
      };

    case GET_RECEIPT:
     
      return {
        ...state,
        receipt: action.payload
      };

    case DELETE_RECEIPT:
      return {
        ...state,
        receipts: state.receipts.filter(
          receipt => receipt.id !== action.payload
        )
      };
    default:
      return state;
  }
}