import { GET_TRADEUNIONS, GET_TRADEUNION, DELETE_TRADEUNION } from "../actions/types";

const initialState = {
  tradeUnions: [],
  tradeUnion: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_TRADEUNIONS:
      
      return {
        ...state,
        tradeUnions: action.payload
      };

    case GET_TRADEUNION:
     
      return {
        ...state,
        tradeUnion: action.payload
      };

    case DELETE_TRADEUNION:
      return {
        ...state,
        tradeUnions: state.tradeUnions.filter(
          tradeUnion => tradeUnion.id !== action.payload
        )
      };
    default:
      return state;
  }
}