import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getSites } from "../../actions/siteActions";
import $ from 'jquery';


class SiteList extends Component {
  constructor() {
    super();
    this.state = {
      errors: {}
    };
    this.addSite = this.addSite.bind(this);
    this.editSite = this.editSite.bind(this);
    this.deleteSite = this.deleteSite.bind(this);
  }

  componentDidMount() {
    this.props.getSites();
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }

    //initialize datatable
    $(document).ready(function () {
      $('#table').DataTable();
    });
  }



  addSite() {
    this.props.history.push("/add-site");
  }
  editSite(siteId) {
    this.props.history.push(`/edit-site/${siteId}`);
  }

  deleteSite(siteId) {
    this.props.deleteSite(siteId);
  }

  render() {
    const { errors } = this.state;
    const { sites } = this.props.site;
    const siteList = sites.map(site => {
      return (
        <tr key={site.id}>
          <td>{site.id}</td>
          <td>{site.siteName}</td>

          <td>
            <div className="btn-group float-right">
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => this.editSite(site.id)}
              >
                Edit
              </button>
              
            </div>
          </td>
        </tr>
      );
    });

    return (
      <div>
        <div className="container mt-5">
         
        <div className="card m-auto  shadow-lg rounded p-3">
              <h1 className="m-auto text-center display-4">Site List</h1>
              <p className="lead text-center">List of all sites</p>
              <hr />
              {(errors.message || errors.status) && (
                <div className="alert alert-danger text-center" role="alert">
                  {(errors && errors.message) || (errors.statusText && errors.statusText)}

                </div>
              )}

              <div className="d-flex justify-content-center pb-2">
                <button
                  className="btn btn-success mt-2"
                  onClick={() => this.addSite()}
                >
                  Add Site
                </button>
              </div>

              <table id="table" className="table table-striped table-bordered">
                <thead>
                  <tr>
                    <th>Id</th>                    
                    <th>Site Name</th>
                    <th style={{ textAlign: "right" }}>Actions</th>
                  </tr>
                </thead>
                <tbody>{siteList}</tbody>
              </table>
            </div>
          
        </div>
      </div>
    );
  }
}
SiteList.propTypes = {
  getSites: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  site: state.site,
  errors: state.errors
});

export default connect(mapStateToProps, { getSites})(SiteList);
