import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getGrades, deleteGrade } from "../../actions/gradeActions";
import $ from 'jquery';


class GradeList extends Component {
  constructor() {
    super();
    this.state = {
      errors: {}
    };
    this.addGrade = this.addGrade.bind(this);
    this.editGrade = this.editGrade.bind(this);
    this.deleteGrade = this.deleteGrade.bind(this);
  }

  componentDidMount() {
    this.props.getGrades();
  }

  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }

    //initialize datatable
    $(document).ready(function () {
      $('#table').DataTable();
    });
  }

  addGrade() {
    this.props.history.push("/add-grade");
  }
  editGrade(gradeId) {
    this.props.history.push(`/edit-grade/${gradeId}`);
  }

  deleteGrade(gradeId) {
    this.props.deleteGrade(gradeId);
  }

  render() {
    const { errors } = this.state;
    const { grades } = this.props.grade;
    const gradeList = grades.map(grade => {
      return (
        <tr key={grade.id}>
          <td>{grade.gradeCode}</td>
          <td>{grade.gradeName}</td>
          <td>{grade.weeklyWage}</td>
          <td>{grade.fortnightlyWage}</td>
          <td>{grade.basicWage}</td>
          <td>{grade.sector.sectorName}</td>
          <td>{grade.currency}</td>
          <td>
            <div className="btn-group float-right">
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => this.editGrade(grade.id)}
              >
                Edit
              </button>
              <button
                type="button"
                className="btn btn-danger"
                onClick={() => this.deleteGrade(grade.id)}
              >
                Delete
              </button>
            </div>
          </td>
        </tr>
      );
    });

    return (
      <div>
        <div className="container-fluid mt-4">

          <div className="card m-auto  shadow-lg rounded p-3">
            <h1 className="m-auto text-center display-4">Grade List</h1>
            <p className="lead text-center">List of all grades</p>
            <hr />
            {(errors.message || errors.status) && (
              <div className="alert alert-danger text-center" role="alert">
                {(errors && errors.message) || (errors.statusText && errors.statusText)}

              </div>
            )}

            <div className="d-flex justify-content-center pb-2">
              <button
                className="fa fa-edit btn btn-success mt-2"
                onClick={() => this.addGrade()}
              >
                Add Grade
                </button>
            </div>

            <table id="table" className="table table-striped table-bordered">
              <thead>
                <tr>
                  <th>Grade Code</th>
                  <th>Grade Name</th>
                  <th>Weekly Wage</th>
                  <th>Fortnightly Wage</th>
                  <th>Basic Wage</th>
                  <th>Sector</th>
                  <th>Currency</th>
                  <th style={{ textAlign: "right" }}>Actions</th>
                </tr>
              </thead>
              <tbody>{gradeList}</tbody>
            </table>

          </div>
        </div>
      </div>
      
    );
  }
}
GradeList.propTypes = {
  getGrades: PropTypes.func.isRequired,
  deleteGrade: PropTypes.func.isRequired,

  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  grade: state.grade,
  errors: state.errors
});

export default connect(mapStateToProps, { getGrades, deleteGrade })(GradeList);
