import axios from "axios";
import { GET_ERRORS,GET_BILLS, GET_BILL, DELETE_BILL } from "./types";

export const createBill = (bill, history) => async dispatch => {
  try {
    
    await axios.post("/api/bills", bill);
    history.push("/process-account");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const billAll = (billDate, history) => async dispatch => {
  try {
    
    await axios.post(`/api/bill-all/${billDate}`);
    history.push("/process-account");
    
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response
    });
  }
};

export const updateBill = (bill, history) => async dispatch => {
  try {
    await axios.put(`/api/bills/${bill.id}`, bill);
    history.push("/process-account");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getBills = () => async dispatch => {
  const res = await axios.get("/api/bills");
  dispatch({
    type: GET_BILLS,
    payload: res.data
    
  });
  
};

export const getBill = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/bills/${id}`);
    
    dispatch({
      type: GET_BILL,
      payload: res.data
    });
  } catch (error) {
    dispatch({
        type: GET_ERRORS,
        payload: error.response.data
      })
  }
};

export const deleteBill = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the bill and all the data related to it"
    )
  ) {
    await axios.delete(`/api/bills/${id}`);
    dispatch({
      type: DELETE_BILL,
      payload: id
    });
  }
};