import { GET_GRADES, GET_GRADE, DELETE_GRADE } from "../actions/types";

const initialState = {
  grades: [],
  grade: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_GRADES:
      
      return {
        ...state,
        grades: action.payload
      };

    case GET_GRADE:
     
      return {
        ...state,
        grade: action.payload
      };

    case DELETE_GRADE:
      return {
        ...state,
        grades: state.grades.filter(
          grade => grade.id !== action.payload
        )
      };
    default:
      return state;
  }
}