import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getSector, updateSector } from "../../actions/sectorActions";
import classnames from "classnames";

class EditSector extends Component {
  constructor() {
    super();
    this.state = {
      id: "",
      sectorName: "",
      sectorCode: "",
      tradeUnion: {},
      errors: {}
    };
    this.saveSector = this.saveSector.bind(this);
    this.cancel = this.cancel.bind(this);
  }


  componentDidMount() {
    const { sectorId } = this.props.match.params;

    this.props.getSector(sectorId, this.props.history);
  }

  //life cycle hooks
  componentWillReceiveProps(nextProps) {

    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }

    const {
      id,
      sectorName,
      sectorCode,
      tradeUnion

    } = nextProps.sector

    this.setState({
      id,
      sectorName,
      sectorCode,
      tradeUnion
    });

  }

  onChange = e => this.setState({ [e.target.name]: e.target.value });


  saveSector = e => {
    e.preventDefault();
    let sector = {
      id: this.state.id,
      sectorName: this.state.sectorName,
      sectorCode: this.state.sectorCode,
      tradeUnion: this.state.tradeUnion

    };

    this.props.updateSector(sector, this.props.history);

  };

  cancel() {
    this.props.history.push("/sector-list");
  }

  render() {
    const { errors } = this.state;

    return (
      <div className="container">

        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">Edit Sector Details</h5>
          <p className="lead text-center">Alter Sector Details</p>
          <hr />
          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.saveSector}>
            <div className="row">
              <div className="form-group col-sm-6">
                <label >Sector Name</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.sectorName })}
                  type="text"
                  name="sectorName"
                  id="sectorName"
                  value={this.state.sectorName}
                  onChange={this.onChange}
                  placeholder="Sector Name"

                />
                {errors.sectorName && (
                  <div className="invalid-feedback">
                    {errors.sectorName}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Sector Code</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.sectorCode })}
                  type="text"
                  name="sectorCode"
                  id="sectorCode"
                  value={this.state.sectorCode}
                  onChange={this.onChange}
                  placeholder="Sector Code"

                />
                {errors.sectorCode && (
                  <div className="invalid-feedback">
                    {errors.sectorCode}
                  </div>
                )}
              </div>
            </div>
            <div className="row">
              <div className="form-group col-sm-6">
                <button
                  type="submit"
                  className="btn btn-primary btn-block mt-4"

                >Save</button>
              </div>
              <div className="form-group col-sm-6">
                <button
                  type="button"
                  className="btn btn-secondary btn-block mt-4"
                  onClick={this.cancel}
                >Cancel</button>
              </div>
            </div>
          </form>

        </div>
      </div>
    );
  }
}

EditSector.propTypes = {
  getSector: PropTypes.func.isRequired,
  sector: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  sector: state.sector.sector,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { getSector, updateSector }
)(EditSector);
