import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getExchangeRate, updateExchangeRate } from "../../actions/exchangeRateActions";
import classnames from "classnames";
import Select from "react-select";

class EditExchangeRate extends Component {
  constructor() {
    super();
    this.state = {
      id: "",
      currency: "",
      rate: "",
      errors: {}
    };
    this.saveExchangeRate = this.saveExchangeRate.bind(this);
    this.cancel = this.cancel.bind(this);
    this.handleCurrencyChange = this.handleCurrencyChange.bind(this);
  }

  componentDidMount() {
    const { exchangeRateId } = this.props.match.params;

    this.props.getExchangeRate(exchangeRateId, this.props.history);
  }

  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }

    const {
      id,
      currency,
      rate
    } = nextProps.exchangeRate;

    this.setState({
      id,
      currency,
      rate
    });
  }
  handleCurrencyChange(val) {
    this.setState({ currency: val.value });
  }
  onChange = e => this.setState({ [e.target.name]: e.target.value });

  saveExchangeRate = e => {
    e.preventDefault();
    let exchangeRate = {
      id: this.state.id,
      currency: this.state.currency,
      rate: this.state.rate

    };

    this.props.updateExchangeRate(exchangeRate, this.props.history);
  };

  cancel() {
    this.props.history.push("/exchangeRate-list");
  }

  render() {
    const { errors } = this.state;
    const currencies = [{ value: "ZWL", label: "ZWL" }, { value: "USD", label: "USD" }]

    return (
      <div className="container">

        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">Edit Exchange Rate Details</h5>
          <p className="lead text-center">Alter Exchange Rate Details</p>
          <hr />
          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.saveExchangeRate}>
            <div className="row">
            <div className="form-group col-sm-6">
                <label>Currency</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleCurrencyChange}
                  value={this.state.currency}
                  options={currencies}
                  value={{label : this.state.currency}}
                  required
                />
              </div>

              <div className="form-group col-sm-6">
                <label >Rate</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.rate })}
                  type="text"
                  name="rate"
                  id="rate"
                  value={this.state.rate}
                  onChange={this.onChange}
                  placeholder="Exchange Rate"

                />
                {errors.rate && (
                  <div className="invalid-feedback">
                    {errors.rate}
                  </div>
                )}
              </div>
            </div>
            <div className="row">
              <div className="form-group col-sm-6">
                <button
                  type="submit"
                  className="btn btn-primary btn-block mt-4"
                >
                  Save
                </button>
              </div>
              <div className="form-group col-sm-6">
                <button
                  type="button"
                  className="btn btn-secondary btn-block mt-4"
                  onClick={this.cancel}
                >
                  Cancel
                </button>
              </div>
            </div>
          </form>
        </div>
      </div>

    );
  }
}

EditExchangeRate.propTypes = {
  getExchangeRate: PropTypes.func.isRequired,
  exchangeRate: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  exchangeRate: state.exchangeRate.exchangeRate,
  errors: state.errors
});

export default connect(mapStateToProps, { getExchangeRate, updateExchangeRate })(
  EditExchangeRate
);
