import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getTradeUnions, deleteTradeUnion } from "../../actions/tradeUnionActions";
import $ from 'jquery';



class TradeUnionList extends Component {
  constructor() {
    super();
    this.state = {
      errors: {}
    };
    this.addTradeUnion = this.addTradeUnion.bind(this);
    this.editTradeUnion = this.editTradeUnion.bind(this);
    this.deleteTradeUnion = this.deleteTradeUnion.bind(this);
  }

  componentDidMount() {
    this.props.getTradeUnions();
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }

    //initialize datatable
    $(document).ready(function () {
      $('#table').DataTable();
    });
  }

  addTradeUnion() {
    this.props.history.push("/add-tradeUnion");
  }
  editTradeUnion(tradeUnionId) {
    this.props.history.push(`/edit-tradeUnion/${tradeUnionId}`);
  }

  deleteTradeUnion(tradeUnionId) {
    this.props.deleteTradeUnion(tradeUnionId);
  }

  render() {
    const { tradeUnions } = this.props.tradeUnion;
    const { errors } = this.state;
    const tradeUnionList = tradeUnions.map(tradeUnion => {
      return (
        <tr key={tradeUnion.id}>
          <td>{tradeUnion.tuName}</td>
          <td>{tradeUnion.address}</td>
          <td>{tradeUnion.phone}</td>
          <td>{tradeUnion.tuPercentage}</td>
          <td>{tradeUnion.baseCurrency}</td>
          <td>
            <div className="btn-group">
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => this.editTradeUnion(tradeUnion.id)}
              >
                Edit
              </button>
              <button
                type="button"
                className="btn btn-danger"
                onClick={() => this.deleteTradeUnion(tradeUnion.id)}
              >
                Delete
              </button>
            </div>
          </td>
        </tr>
      );
    });

    return (
      <div>
        <div className="container mt-4">
         
        <div className="card m-auto  shadow-lg rounded p-3">
              <h1 className="m-auto text-center display-4">Trade Union List</h1>
              <p className="lead text-center">List of all trade unions</p>
              <hr />
              {(errors.message || errors.status) && (
                <div className="alert alert-danger text-center" role="alert">
                  {(errors && errors.message) || (errors.statusText && errors.statusText)}

                </div>
              )}
              <div className="d-flex justify-content-center pb-2">
                <button
                  className="btn btn-success mt-2"
                  onClick={() => this.addTradeUnion()}
                >
                  Add Trade Union
                </button>
              </div>

              <table id="table" className="table table-striped table-bordered">
                <thead>
                  <tr>
                    <th>Trade Union</th>
                    <th>Address</th>
                    <th>Phone</th>
                    <th>TU Percentage</th>
                    <th>Base Currency</th>
                    <th>Actions</th>
                  </tr>
                </thead>
                <tbody>{tradeUnionList}</tbody>
              </table>
            </div>
          </div>
        </div>
     
    );
  }
}
TradeUnionList.propTypes = {
  getTradeUnions: PropTypes.func.isRequired,
  deleteTradeUnion: PropTypes.func.isRequired,

  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  tradeUnion: state.tradeUnion,
  errors: state.errors
});

export default connect(mapStateToProps, { getTradeUnions, deleteTradeUnion })(TradeUnionList);
