import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createGrade } from "../../actions/gradeActions";
import { getSectors } from "../../actions/sectorActions";
import Select from 'react-select';

import classnames from "classnames";

class NewGrade extends Component {
  constructor() {
    super();
    this.state = {
      gradeName: "",
      gradeCode: "",
      weeklyWage: "",
      fortnightlyWage: "",
      basicWage: "",
      currency:"",
      sector: {},
      errors: {}
    };
    this.saveGrade = this.saveGrade.bind(this);
    this.cancel = this.cancel.bind(this);
    this.handleSectorChange = this.handleSectorChange.bind(this);
    this.handleCurrencyChange = this.handleCurrencyChange.bind(this);
  }

  componentDidMount() {
    this.props.getSectors();
  }
  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange = e => this.setState({ [e.target.name]: e.target.value });

  handleSectorChange(val) {
    this.setState({ sector: val });
  }

  handleCurrencyChange(val) {
    this.setState({ currency: val.value });
  }

  saveGrade = e => {
    e.preventDefault();
    let grade = {
      gradeName: this.state.gradeName,
      gradeCode: this.state.gradeCode,
      weeklyWage: this.state.weeklyWage,
      fortnightlyWage: this.state.fortnightlyWage,
      basicWage: this.state.basicWage,
      currency:this.state.currency,
      sector: this.state.sector,

    };

    this.props.createGrade(grade, this.props.history);

  };

  cancel() {
    this.props.history.push("/grade-list");
  }

  render() {
    const { errors } = this.state;
    const { sectors } = this.props.sector
    const currencies=[{value:"ZWL",label:"ZWL"},{value:"USD",label:"USD"}]

    return (
      <div className="container">

        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">New Grade Type</h5>
          <p className="lead text-center">Enter details for new Grade</p>
          <hr />
          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.saveGrade}>
            <div className="row">
              <div className="form-group col-sm-6">
                <label >Grade Code</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.gradeCode })}
                  type="text"
                  name="gradeCode"
                  id="gradeCode"
                  value={this.state.gradeCode}
                  onChange={this.onChange}
                  placeholder="Grade Code"

                />
                {errors.gradeCode && (
                  <div className="invalid-feedback">
                    {errors.gradeCode}
                  </div>
                )}
              </div>
              <div className="form-group col-sm-6">
                <label >Grade Name</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.gradeName })}
                  type="text"
                  name="gradeName"
                  id="gradeName"
                  value={this.state.gradeName}
                  onChange={this.onChange}
                  placeholder="Grade Name"

                />
                {errors.gradeName && (
                  <div className="invalid-feedback">
                    {errors.gradeName}
                  </div>
                )}
              </div>
              <div className="form-group col-sm-6">
                <label >Weekly Wage</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.weeklyWage })}
                  type="text"
                  name="weeklyWage"
                  id="weeklyWage"
                  value={this.state.weeklyWage}
                  onChange={this.onChange}
                  placeholder="Weekly Wage"

                />
                {errors.weeklyWage && (
                  <div className="invalid-feedback">
                    {errors.weeklyWage}
                  </div>
                )}
              </div>
              <div className="form-group col-sm-6">
                <label >Fortnightly Wage</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.fortnightlyWage })}
                  type="text"
                  name="fortnightlyWage"
                  id="fortnightlyWage"
                  value={this.state.fortnightlyWage}
                  onChange={this.onChange}
                  placeholder="Fortnightly Wage"

                />
                {errors.fortnightlyWage && (
                  <div className="invalid-feedback">
                    {errors.fortnightlyWage}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Basic Wage</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.basicWage })}
                  type="text"
                  name="basicWage"
                  id="basicWage"
                  value={this.state.basicWage}
                  onChange={this.onChange}
                  placeholder="Basic Wage"

                />
                {errors.basicWage && (
                  <div className="invalid-feedback">
                    {errors.basicWage}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label>Sector</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleSectorChange}
                  getOptionLabel={sector => `${sector.sectorName}`}
                  isOptionSelected={true}
                  getOptionValue={sector => `${sector}`}
                  options={sectors}
                />
              </div>
              <div className="form-group col-sm-6">
                <label>Currency</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleCurrencyChange}
                  options={currencies}
                  required
                />
              </div>

            </div>
            <div className="row">

              <div className="form-group col-sm-6">
                <button
                  type="submit"
                  className="btn btn-primary btn-block mt-4"

                >Save</button>
              </div>
              <div className="form-group col-sm-6">
                <button
                  type="button"
                  className="btn btn-secondary btn-block mt-4"
                  onClick={this.cancel}
                >Cancel</button>

              </div>
            </div>
          </form>
        </div>
      </div >

    );
  }
}

NewGrade.propTypes = {
  createGrade: PropTypes.func.isRequired,
  getSectors: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  sector: state.sector,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { createGrade, getSectors }
)(NewGrade);
