import { GET_BATCHES, GET_BATCH, DELETE_BATCH} from "../actions/types";



const initialState = {
  batches: [],
  batch: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_BATCHES:
      
      return {
        ...state,
        batches: action.payload
      };

    case GET_BATCH:
     
      return {
        ...state,
        batch: action.payload
      };

    case DELETE_BATCH:
      return {
        ...state,
        batches: state.batches.filter(
          batch => batch.id !== action.payload
        )
      };
    default:
      return state;
  }
}