import axios from "axios";
import { GET_ERRORS,GET_EXCHANGERATES, GET_EXCHANGERATE, DELETE_EXCHANGERATE } from "./types";

const FileDownload = require('js-file-download');

export const createExchangeRate = (exchangeRate, history) => async dispatch => {
  try {
    await axios.post("/rates/new", exchangeRate);
    history.push("/exchangerate-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateExchangeRate = (exchangeRate, history) => async dispatch => {
  try {
    await axios.put(`/rates/update/${exchangeRate.id}`, exchangeRate);
    history.push("/exchangerate-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getExchangeRates = () => async dispatch => {
  const res = await axios.get("/rates/all");
  dispatch({
    type: GET_EXCHANGERATES,
    payload: res.data
    
  });

  dispatch({
    type: GET_ERRORS,
    payload: {}
  });
  
};

export const getExchangeRate = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/rates/by-id/${id}`);
    
    dispatch({
      type: GET_EXCHANGERATE,
      payload: res.data
    });
  } catch (error) {
    
    history.push("/home");
  }
};

export const deleteExchangeRate = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the exchangeRate and all the data related to it"
    )
  ) {
    await axios.delete(`/rates/delete/${id}`);
    dispatch({
      type: DELETE_EXCHANGERATE,
      payload: id
    });
  }
};


