import { GET_INSTITUTIONS, GET_INSTITUTION, GET_MEMBERINSTITUTION, DELETE_INSTITUTION } from "../actions/types";

const initialState = {
  institutions: [],
  institution: {},
  memberinstitution:[]
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_INSTITUTIONS:
      
      return {
        ...state,
        institutions: action.payload
      };

      case GET_MEMBERINSTITUTION:
      
      return {
        ...state,
        memberinstitution: action.payload
      };

    case GET_INSTITUTION:
     
      return {
        ...state,
        institution: action.payload
      };

    case DELETE_INSTITUTION:
      return {
        ...state,
        institutions: state.institutions.filter(
          institution => institution.id !== action.payload
        )
      };
    default:
      return state;
  }
}