import axios from "axios";
import { GET_ERRORS,GET_BRANCHES, GET_BRANCH, DELETE_BRANCH } from "./types";

const FileDownload = require('js-file-download');

export const createBranch = (branch, history) => async dispatch => {
  try {
    await axios.post("/api/branches", branch);
    history.push("/branch-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateBranch = (branch, history) => async dispatch => {
  try {
    await axios.put(`/api/branches/${branch.id}`, branch);
    history.push("/branch-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getBranches = () => async dispatch => {
  const res = await axios.get("/api/branches");
  dispatch({
    type: GET_BRANCHES,
    payload: res.data
    
  });

  dispatch({
    type: GET_ERRORS,
    payload: {}
  });
  
};

export const getBranch = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/branches/${id}`);
    
    dispatch({
      type: GET_BRANCH,
      payload: res.data
    });
  } catch (error) {
    
    history.push("/home");
  }
};

export const deleteBranch = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the branch and all the data related to it"
    )
  ) {
    await axios.delete(`/api/branches/${id}`);
    dispatch({
      type: DELETE_BRANCH,
      payload: id
    });
  }
};


