import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createSite } from "../../actions/siteActions";

import classnames from "classnames";

class NewSite extends Component {
  constructor() {
    super();
    this.state = {
      siteName: "",
      errors: {}
    };
    this.saveSite = this.saveSite.bind(this);
    this.cancel = this.cancel.bind(this);
  }


  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange = e => this.setState({ [e.target.name]: e.target.value });


  saveSite = e => {
    e.preventDefault();
    let site = {
      siteName: this.state.siteName

    };

    this.props.createSite(site, this.props.history);

  };

  cancel() {
    this.props.history.push("/site-list");
  }

  render() {
    const { errors } = this.state;

    return (
      <div className="container">

        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">New Site</h5>
          <p className="lead text-center">New Site Details</p>
          <hr />
          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.saveSite}>
            <div className="row">
              <div className="form-group col-sm-12 ">
                <label >Site Name</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.siteName })}
                  type="text"
                  name="siteName"
                  id="siteName"
                  value={this.state.siteName}
                  onChange={this.onChange}
                  placeholder="Site Name"

                />
                {errors.siteName && (
                  <div className="invalid-feedback">
                    {errors.siteName}
                  </div>
                )}
              </div>
            </div>
            <div className="row">
              <div className="form-group col-sm-12">
                <button
                  type="submit"
                  className="btn btn-primary btn-block mt-4"

                >Save</button>
              </div>
              <div className="form-group col-sm-12">
                <button
                  type="button"
                  className="btn btn-secondary btn-block mt-4"
                  onClick={this.cancel}
                >Cancel</button>
              </div>
            </div>
          </form>
        </div>

      </div>
    );
  }
}

NewSite.propTypes = {
  createSite: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { createSite }
)(NewSite);
