import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { updateInstitution, getInstitution } from "../../actions/institutionActions";
import { getSectors } from "../../actions/sectorActions";
import { getBranches } from "../../actions/branchActions";
import classnames from "classnames";
import Select from "react-select";

class EditInstitution extends Component {

  constructor() {
    super();

    this.state = {
      id: "",
      institutionName: "",
      address: "",
      phone: "",
      email: "",
      sector: "",
      currency: "",
      tuPercentage: "",
      branch:'',
      errors: {}
    };

    this.updateInstitution = this.updateInstitution.bind(this);
    this.cancel = this.cancel.bind(this);
    this.handleSectorChange = this.handleSectorChange.bind(this);
    this.handleBranchChange = this.handleBranchChange.bind(this);
    this.handleCurrencyChange = this.handleCurrencyChange.bind(this);

  }

  componentDidMount() {

    const { institutionId } = this.props.match.params;
    this.props.getInstitution(institutionId, this.props.history);
    this.props.getSectors();
    this.props.getBranches();
  }

  //life cycle hooks
  componentWillReceiveProps(nextProps) {

    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }


    const {
      id,
      institutionName,
      address,
      phone,
      email,
      sector,
      currency,
      tuPercentage,
      branch

    } = nextProps.institution;

    this.setState({
      id,
      institutionName,
      address,
      phone,
      email,
      sector,
      currency,
      tuPercentage,
      branch
    });
  }

  onChange = e =>
    this.setState({
      [e.target.name]: e.target.value
    });

  handleSectorChange(val) {
    this.setState({ sector: val });
  }

  handleBranchChange(val) {

    this.setState({ branch: val });
  }
  handleCurrencyChange(val) {
    this.setState({ currency: val.value });
  }
  updateInstitution = e => {

    e.preventDefault();

    let institution = {
      id: this.state.id,
      institutionName: this.state.institutionName,
      address: this.state.address,
      phone: this.state.phone,
      email: this.state.email,
      sector: this.state.sector,
      branch:this.state.branch,
      currency:this.state.currency,
      tuPercentage: this.state.tuPercentage
    };

    this.props.updateInstitution(institution, this.props.history);
  };

  cancel() {
    this.props.history.push("/institution-list");
  }


  render() {

    const { sectors } = this.props.sector;
    const { errors } = this.state;
    const { branches } = this.props.branch;
    const currencies=[{value:"ZWL",label:"ZWL"},{value:"USD",label:"USD"}]

    return (
      <div className="container">
        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">Edit Institution</h5>
          <p className="lead text-center">Alter Institution Details</p>
          <hr />
          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.updateInstitution}>
            <div className="row">
              <div className="form-group col-sm-6">
                <label >Institution Name</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.institutionName })}
                  type="text"
                  name="institutionName"
                  id="institutionName"
                  value={this.state.institutionName}
                  onChange={this.onChange}
                  placeholder="Institution Name"

                />
                {errors.institutionName && (
                  <div className="invalid-feedback">
                    {errors.institutionName}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Institution Address</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.address })}
                  type="text"
                  name="address"
                  id="address"
                  value={this.state.address}
                  onChange={this.onChange}
                  placeholder="Institution Address"

                />
                {errors.address && (
                  <div className="invalid-feedback">
                    {errors.address}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Institution Phone</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.phone })}
                  type="text"
                  name="phone"
                  id="phone"
                  value={this.state.phone || ""}
                  onChange={this.onChange}
                  placeholder="Institution Phone"

                />
                {errors.phone && (
                  <div className="invalid-feedback">
                    {errors.phone}
                  </div>
                )}
              </div>



              <div className="form-group col-sm-6">
                <label>Email</label>
                <input
                  className={classnames("form-control", {
                    "is-invalid": errors.email
                  })}
                  type="text"
                  name="email"
                  id="email"
                  value={this.state.email}
                  onChange={this.onChange}
                  placeholder="Email"
                />
                {errors.email && (
                  <div className="invalid-feedback">{errors.email}</div>
                )}
              </div>


              <div className="form-group col-sm-6">
                <label>Sector</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleSectorChange}
                  getOptionLabel={sector => `${sector.sectorName}`}
                  isOptionSelected={true}
                  value={this.state.sector}
                  options={sectors}
                />
              </div>

              <div className="form-group col-sm-6">
              <label>Branch</label>
              <Select
                className="react-selectcomponent"
                classNamePrefix="name-select"
                onChange={this.handleBranchChange}
                getOptionLabel={(branch) => `${branch.branchName}`}
                isOptionSelected={true}
                value={this.state.branch}
                required="true"
                options={branches}
              />
            </div>

              <div className="form-group col-sm-6">
                <label>TU Percentage</label>
                <input
                  className={classnames("form-control", {
                    "is-invalid": errors.tuPercentage
                  })}
                  type="number"
                  min="0"
                  name="tuPercentage"
                  id="tuPercentage"
                  value={this.state.tuPercentage||""}
                  onChange={this.onChange}
                  placeholder="TU Percentage"
                />
                {errors.tuPercentage && (
                  <div className="invalid-feedback">{errors.tuPercentage}</div>
                )}
              </div>
              <div className="form-group col-sm-6">
                <label>Currency</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleCurrencyChange}
                  value={this.state.currency}
                  options={currencies}
                  value={{label : this.state.currency}}
                  required
                />
              </div>
            </div>
            <div className="row">
              <div className="form-group col-sm-6">
                <button
                  className="btn btn-primary btn-block"
                  type="submit">Save</button>
              </div>
              <div className="form-group col-sm-6">
                <button className="btn btn-secondary btn-block" onClick={this.cancel}>Cancel</button>
              </div>
            </div>
          </form>
        </div>
      </div>

    );
  }
}

EditInstitution.propTypes = {
  updateInstitution: PropTypes.func.isRequired,
  getBranches: PropTypes.func.isRequired,
  getSectors: PropTypes.func.isRequired,
  getInstitution: PropTypes.func.isRequired,
  institution: PropTypes.object.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  sector: state.sector,
  branch: state.branch,
  institution: state.institution.institution,
  errors: state.errors
});

export default connect(mapStateToProps, {
  updateInstitution,
  getSectors,
  getInstitution,
  getBranches

})(EditInstitution);
