import axios from "axios";
import { GET_ERRORS,GET_RECEIPTS, GET_RECEIPT, DELETE_RECEIPT } from "./types";

export const createReceipt = (receipt, history) => async dispatch => {
  try {
    
    await axios.post("/api/receipts", receipt);
    history.push("/process-account");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateReceipt = (receipt, history) => async dispatch => {
  try {
    await axios.put(`/api/receipts/${receipt.id}`, receipt);
    history.push("/process-account");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getReceipts = () => async dispatch => {
  const res = await axios.get("/api/receipts");
  dispatch({
    type: GET_RECEIPTS,
    payload: res.data
    
  });
  
};

export const getReceipt = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/receipts/${id}`);
    
    dispatch({
      type: GET_RECEIPT,
      payload: res.data
    });
  } catch (error) {
    dispatch({
        type: GET_ERRORS,
        payload: error.response.data
      })
  }
};

export const deleteReceipt = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the receipt and all the data related to it"
    )
  ) {
    await axios.delete(`/api/receipts/${id}`);
    dispatch({
      type: DELETE_RECEIPT,
      payload: id
    });
  }
};