import { GET_SECTORS, GET_SECTOR, DELETE_SECTOR } from "../actions/types";

const initialState = {
  sectors: [],
  sector: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_SECTORS:
      
      return {
        ...state,
        sectors: action.payload
      };

    case GET_SECTOR:
     
      return {
        ...state,
        sector: action.payload
      };

    case DELETE_SECTOR:
      return {
        ...state,
        sectors: state.sectors.filter(
          sector => sector.id !== action.payload
        )
      };
    default:
      return state;
  }
}