import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getBranches, deleteBranch, viewMembers } from "../../actions/branchActions";
import $ from 'jquery';
import { CircularProgress } from '@material-ui/core';

const FileDownload = require('js-file-download');

class BranchList extends Component {
  constructor() {
    super();
    this.state = {
      buttonState: '',
      errors: {}
    };
    this.addBranch = this.addBranch.bind(this);
    this.editBranch = this.editBranch.bind(this);
    this.deleteBranch = this.deleteBranch.bind(this);
    this.viewMembers = this.viewMembers.bind(this);
  }

  componentDidMount() {
    this.props.getBranches();
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }

    //initialize datatable
    $(document).ready(function () {
      $('#table').DataTable();
    });
  }


  addBranch() {
    this.props.history.push("/add-branch");
  }
  editBranch(branchId) {
    this.props.history.push(`/edit-branch/${branchId}`);
  }
  viewMembers(branchId) {
    this.setState({ buttonState: "loading" })
    this.props.viewMembers(branchId);
    this.setState({ buttonState: "complete" })
  }

  deleteBranch(branchId) {
    this.props.deleteBranch(branchId);
  }

  render() {
    const { errors } = this.state;
    const { branches } = this.props.branch;
    const branchList = branches.map(branch => {
      return (
        <tr key={branch.id}>
          <td>{branch.branchName}</td>
          <td>{branch.branchAddress}</td>
          <td>{branch.email}</td>
          <td>{branch.branchPhone}</td>
          <td>{branch.branchTown}</td>

          <td>
            <div className="btn-group float-right">
              <button
                type="button"
                className="btn btn-success"
                onClick={() => this.viewMembers(branch.id)}
              >

                View Members
              </button>
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => this.editBranch(branch.id)}
              >
                Edit
              </button>
              <button
                type="button"
                className="btn btn-danger"
                onClick={() => this.deleteBranch(branch.id)}
              >
                Delete
              </button>
            </div>
          </td>
        </tr>
      );
    });

    return (
      <div>
        <div className="container-fluid mt-5">

          <div className="card m-auto  shadow-lg rounded p-3">
            <h1 className="m-auto text-center display-4">Branch List</h1>
            <p className="lead text-center">List of all branches</p>
            <hr />
            {(errors.message || errors.status) && (
              <div className="alert alert-danger text-center" role="alert">
                {(errors && errors.message) || (errors.statusText && errors.statusText)}

              </div>
            )}

            <div className="d-flex justify-content-center pb-2">
              <button
                className="btn btn-success mt-2"
                onClick={() => this.addBranch()}
              >
                Add Branch
                </button>
            </div>

            <table id="table" className="table table-striped table-bordered">
              <thead>
                <tr>
                  <th>Branch Name</th>
                  <th>Address</th>
                  <th>Email</th>
                  <th>Phone</th>
                  <th>Town</th>
                  <th style={{ textAlign: "right" }}> {this.state.buttonState === "loading" && <CircularProgress />}Actions</th>
                </tr>
              </thead>
              <tbody>{branchList}</tbody>
            </table>
          </div>

        </div>
      </div>
    );
  }
}
BranchList.propTypes = {
  getBranches: PropTypes.func.isRequired,
  deleteBranch: PropTypes.func.isRequired,
  viewMembers: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  branch: state.branch,
  errors: state.errors
});

export default connect(mapStateToProps, { getBranches, deleteBranch, viewMembers })(BranchList);
