import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { getSectors, deleteSector } from "../../actions/sectorActions";
import $ from 'jquery';


class SectorList extends Component {
  constructor() {
    super();
    this.state = {
      errors: {}
    };
    this.addSector = this.addSector.bind(this);
    this.editSector = this.editSector.bind(this);
    this.deleteSector = this.deleteSector.bind(this);
  }

  componentDidMount() {
    this.props.getSectors();
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }

    //initialize datatable
    $(document).ready(function () {
      $('#table').DataTable();
    });
  }



  addSector() {
    this.props.history.push("/add-sector");
  }
  editSector(sectorId) {
    this.props.history.push(`/edit-sector/${sectorId}`);
  }

  deleteSector(sectorId) {
    this.props.deleteSector(sectorId);
  }

  render() {
    const { errors } = this.state;
    const { sectors } = this.props.sector;
    const sectorList = sectors.map(sector => {
      return (
        <tr key={sector.id}>
          <td>{sector.id}</td>
          <td>{sector.sectorCode}</td>
          <td>{sector.sectorName}</td>

          <td>
            <div className="btn-group float-right">
              <button
                type="button"
                className="btn btn-primary"
                onClick={() => this.editSector(sector.id)}
              >
                Edit
              </button>
              <button
                type="button"
                className="btn btn-danger"
                onClick={() => this.deleteSector(sector.id)}
              >
                Delete
              </button>
            </div>
          </td>
        </tr>
      );
    });

    return (
      <div>
        <div className="container mt-5">
         
        <div className="card m-auto  shadow-lg rounded p-3">
              <h1 className="m-auto text-center display-4">Sector List</h1>
              <p className="lead text-center">List of all sectors</p>
              <hr />
              {(errors.message || errors.status) && (
                <div className="alert alert-danger text-center" role="alert">
                  {(errors && errors.message) || (errors.statusText && errors.statusText)}

                </div>
              )}

              <div className="d-flex justify-content-center pb-2">
                <button
                  className="btn btn-success mt-2"
                  onClick={() => this.addSector()}
                >
                  Add Sector
                </button>
              </div>

              <table id="table" className="table table-striped table-bordered">
                <thead>
                  <tr>
                    <th>Id</th>
                    <th>Sector Code</th>
                    <th>Sector Name</th>
                    <th style={{ textAlign: "right" }}>Actions</th>
                  </tr>
                </thead>
                <tbody>{sectorList}</tbody>
              </table>
            </div>
          
        </div>
      </div>
    );
  }
}
SectorList.propTypes = {
  getSectors: PropTypes.func.isRequired,
  deleteSector: PropTypes.func.isRequired,

  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  sector: state.sector,
  errors: state.errors
});

export default connect(mapStateToProps, { getSectors, deleteSector })(SectorList);
