import axios from "axios";
import { GET_ERRORS,GET_GRADES, GET_GRADE, DELETE_GRADE } from "./types";

export const createGrade = (grade, history) => async dispatch => {
  try {
    await axios.post("/api/grades", grade);
    history.push("/grade-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateGrade = (grade, history) => async dispatch => {
  try {
    await axios.put(`/api/grades/${grade.id}`, grade);
    history.push("/grade-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getGrades = () => async dispatch => {
  const res = await axios.get("/api/grades");
  dispatch({
    type: GET_GRADES,
    payload: res.data
    
  });
  dispatch({
    type: GET_ERRORS,
    payload: {}
  });
  
};

export const getGrade = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/grades/${id}`);
    
    dispatch({
      type: GET_GRADE,
      payload: res.data
    });
  } catch (error) {
    
    history.push("/home");
  }
};

export const deleteGrade = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the grade and all the data related to it"
    )
  ) {
    await axios.delete(`/api/grades/${id}`);
    dispatch({
      type: DELETE_GRADE,
      payload: id
    });
  }
};