import { GET_SITES, GET_SITE, DELETE_SITE} from "../actions/types";



const initialState = {
  sites: [],
  site: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_SITES:
      
      return {
        ...state,
        sites: action.payload
      };

    case GET_SITE:
     
      return {
        ...state,
        site: action.payload
      };

    case DELETE_SITE:
      return {
        ...state,
        sites: state.sites.filter(
          site => site.id !== action.payload
        )
      };
    default:
      return state;
  }
}