import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createBranch } from "../../actions/branchActions";

import classnames from "classnames";

class NewBranch extends Component {
  constructor() {
    super();
    this.state = {
      branchName: "",
      branchAddress: "",
      branchPhone: "",
      branchTown: "",
      email: "",
      tradeUnion: {},
      errors: {}
    };
    this.saveBranch = this.saveBranch.bind(this);
    this.cancel = this.cancel.bind(this);
  }


  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange = e => this.setState({ [e.target.name]: e.target.value });


  saveBranch = e => {
    e.preventDefault();
    let branch = {
      branchName: this.state.branchName,
      branchAddress: this.state.branchAddress,
      branchPhone: this.state.branchPhone,
      branchTown: this.state.branchTown,
      email: this.state.email,
      tradeUnion: {}

    };

    this.props.createBranch(branch, this.props.history);

  };

  cancel() {
    this.props.history.push("/branch-list");
  }

  render() {
    const { errors } = this.state;

    return (
      <div className="container">

        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">New Branch</h5>
          <p className="lead text-center">Add New Branch Details</p>
          <hr />

          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.saveBranch}>
            <div className="row">
              <div className="form-group col-sm-6">
                <label >Branch Name</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.branchName })}
                  type="text"
                  name="branchName"
                  id="branchName"
                  value={this.state.branchName}
                  onChange={this.onChange}
                  placeholder="Branch Name"

                />
                {errors.branchName && (
                  <div className="invalid-feedback">
                    {errors.branchName}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Branch Address</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.branchAddress })}
                  type="text"
                  name="branchAddress"
                  id="branchAddress"
                  value={this.state.branchAddress}
                  onChange={this.onChange}
                  placeholder="Branch Address"

                />
                {errors.branchAddress && (
                  <div className="invalid-feedback">
                    {errors.branchAddress}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label>Email</label>
                <input
                  className={classnames("form-control", {
                    "is-invalid": errors.email,
                  })}
                  type="text"
                  name="email"
                  id="email"
                  value={this.state.email || ""}
                  onChange={this.onChange}
                  placeholder="Email"
                />
                {errors.email && (
                  <div className="invalid-feedback">{errors.email}</div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Branch Phone</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.branchPhone })}
                  type="text"
                  name="branchPhone"
                  id="branchPhone"
                  value={this.state.branchPhone}
                  onChange={this.onChange}
                  placeholder="Branch Phone"

                />
                {errors.branchPhone && (
                  <div className="invalid-feedback">
                    {errors.branchPhone}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Branch Town</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.branchTown })}
                  type="text"
                  name="branchTown"
                  id="branchTown"
                  value={this.state.branchTown}
                  onChange={this.onChange}
                  placeholder="Branch Town"

                />
                {errors.branchTown && (
                  <div className="invalid-feedback">
                    {errors.branchTown}
                  </div>
                )}
              </div>


            </div>
            <div className="row">
              <div className="form-group col-sm-6">
                <button
                  type="submit"
                  className="btn btn-primary btn-block mt-4"

                >Save</button>
              </div>
              <div className="form-group col-sm-6">
                <button
                  type="button"
                  className="btn btn-secondary btn-block mt-4"
                  onClick={this.cancel}
                >Cancel</button>
              </div>
            </div>
          </form>

        </div>
      </div>
    );
  }
}

NewBranch.propTypes = {
  createBranch: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { createBranch }
)(NewBranch);
