import axios from "axios";
import { GET_ERRORS, SET_CURRENT_USER,GET_USERS,GET_ROLES } from "./types";
import setJWTToken from "../securityUtils/setJWTToken";
import jwt_decode from "jwt-decode";

export const createNewUser = (newUser, history) => async dispatch => {
  try {
    await axios.post("/api/users/register-user", newUser);
    history.push("/login");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const login = LoginRequest => async dispatch => {
  try {
    // post => Login Request
    const res = await axios.post("/api/users/login", LoginRequest);
    // extract token from res.data
    const { token } = res.data;
    // store the token in the localStorage
    localStorage.setItem("jwtToken", token);
    // set our token in header ***
    setJWTToken(token);
    // decode token on React
    const decoded = jwt_decode(token);
    // dispatch to our securityReducer
    dispatch({
      type: SET_CURRENT_USER,
      payload: decoded
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateUser = (user, history) => async dispatch => {
  try {
    await axios.put(`/api/user/update-user/${user.id}`, user);
    history.push("/edit-user");
    
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};


export const getUsers = () => async dispatch => {
  const res = await axios.get("/api/users/users");
  
  dispatch({
    type: GET_USERS,
    payload: res.data
    
  });
  
};

export const getRoles = () => async dispatch => {
  const res = await axios.get("/api/users/roles");
  
  dispatch({
    type: GET_ROLES,
    payload: res.data
    
  });
  
};




export const logout = () => dispatch => {
  localStorage.removeItem("jwtToken");
  setJWTToken(false);
  dispatch({
    type: SET_CURRENT_USER,
    payload: {}
  });
};
