import axios from "axios";
import { GET_ERRORS,GET_TRADEUNIONS, GET_TRADEUNION, DELETE_TRADEUNION } from "./types";

export const createTradeUnion = (tradeUnion, history) => async dispatch => {
  try {
    await axios.post("/api/trade-unions", tradeUnion);
    history.push("/tradeUnion-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateTradeUnion = (tradeUnion, history) => async dispatch => {
  try {
    await axios.put(`/api/trade-unions/${tradeUnion.id}`, tradeUnion);
    history.push("/tradeUnion-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getTradeUnions = () => async dispatch => {
  const res = await axios.get("/api/trade-unions");
  dispatch({
    type: GET_TRADEUNIONS,
    payload: res.data
    
  });
  
};

export const getTradeUnion = (id, history) => async dispatch => {
  try {
    const res = await axios.get(`/api/trade-unions/${id}`);
    
    dispatch({
      type: GET_TRADEUNION,
      payload: res.data
    });
  } catch (error) {
    
    history.push("/home");
  }
};

export const deleteTradeUnion = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the tradeUnion and all the data related to it"
    )
  ) {
    await axios.delete(`/api/trade-unions/${id}`);
    dispatch({
      type: DELETE_TRADEUNION,
      payload: id
    });
  }
};