import axios from "axios";
import { GET_ERRORS,GET_SECTORS, GET_SECTOR, DELETE_SECTOR } from "./types";

export const createSector = (sector, history) => async dispatch => {
  try {
    await axios.post("/api/sectors", sector);
    history.push("/sector-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const updateSector = (sector, history) => async dispatch => {
  try {
    await axios.put(`/api/sectors/${sector.id}`, sector);
    history.push("/sector-list");
    dispatch({
      type: GET_ERRORS,
      payload: {}
    });
  } catch (err) {
    dispatch({
      type: GET_ERRORS,
      payload: err.response.data
    });
  }
};

export const getSectors = () => async dispatch => {
  const res = await axios.get("/api/sectors");
  dispatch({
    type: GET_SECTORS,
    payload: res.data
    
  });

  dispatch({
    type: GET_ERRORS,
    payload: {}
  });
  
};

export const getSector = (id, history) => async dispatch => {
  try {
    
    const res = await axios.get(`/api/sectors/${id}`);
    
    dispatch({
      type: GET_SECTOR,
      payload: res.data
    });
  } catch (error) {
   
    history.push("/home");
  }
};

export const deleteSector = id => async dispatch => {
  if (
    window.confirm(
      "Are you sure? This will delete the sector and all the data related to it"
    )
  ) {
    await axios.delete(`/api/sectors/${id}`);
    dispatch({
      type: DELETE_SECTOR,
      payload: id
    });
  }
};