import { GET_EXCHANGERATES, GET_EXCHANGERATE, DELETE_EXCHANGERATE } from "../actions/types";

const initialState = {
  exchangeRates: [],
  exchangeRate: {}
};

export default function(state = initialState, action) {
  switch (action.type) {
    case GET_EXCHANGERATES:
      
      return {
        ...state,
        exchangeRates: action.payload
      };

    case GET_EXCHANGERATE:
     
      return {
        ...state,
        exchangeRate: action.payload
      };

    case DELETE_EXCHANGERATE:
      return {
        ...state,
        exchangeRates: state.exchangeRates.filter(
          exchangeRate => exchangeRate.id !== action.payload
        )
      };
    default:
      return state;
  }
}