import { SET_CURRENT_USER,GET_USERS,GET_ROLES } from "../actions/types";

const initialState = {
  validToken: false,
  users:[],
  user: null,
  roles:[]
};

const booleanActionPayload = payload => {
  if (payload) {
    return true;
  } else {
    return false;
  }
};

export default function(state = initialState, action) {
  switch (action.type) {
    case SET_CURRENT_USER:
      return {
        ...state,
        validToken: booleanActionPayload(action.payload),
        user: action.payload
      };
      case GET_USERS:
        return {
          ...state,
          users: action.payload
        };

        case GET_ROLES:
          return {
            ...state,
            roles: action.payload
          };

    default:
      return state;
  }
}
