import React, { Component } from "react";
import PropTypes from "prop-types";
import { connect } from "react-redux";
import { createInstitution } from "../../actions/institutionActions";
import { getBranches } from "../../actions/branchActions";
import { getSectors } from "../../actions/sectorActions";
import Select from 'react-select';

import classnames from "classnames";

class NewInstitution extends Component {
  constructor() {
    super();
    this.state = {
      institutionName: "",
      address: "",
      phone: "",
      email: "",
      sector: "",
      currency: "",
      tuPercentage: "",
      branch:'',
      errors: {}
    };
    this.saveInstitution = this.saveInstitution.bind(this);
    this.cancel = this.cancel.bind(this);
    this.handleSectorChange = this.handleSectorChange.bind(this);    
    this.handleCurrencyChange = this.handleCurrencyChange.bind(this);
    this.handleBranchChange = this.handleBranchChange.bind(this);
    
  }

  componentDidMount() {
    this.props.getBranches();
    this.props.getSectors();
  }
  //life cycle hooks
  componentWillReceiveProps(nextProps) {
    if (nextProps.errors) {
      this.setState({ errors: nextProps.errors });
    }
  }

  onChange = e => this.setState({ [e.target.name]: e.target.value });

  handleSectorChange(val) {
    this.setState({ sector: val });
  }
  handleCurrencyChange(val) {
    this.setState({ currency: val.value });
  }

  handleBranchChange(val) {

    this.setState({ branch: val });
  }

  saveInstitution = e => {
    e.preventDefault();
    let institution = {
      institutionName: this.state.institutionName,
      address: this.state.address,
      phone: this.state.phone,
      email: this.state.email,
      sector: this.state.sector,
      currency:this.state.currency,
      branch:this.state.branch,
      tuPercentage: this.state.tuPercentage


    };

    this.props.createInstitution(institution, this.props.history);

  };

  cancel() {
    this.props.history.push("/institution-list");
  }

  render() {
    const { errors } = this.state;
    const { sectors } = this.props.sector
    const { branches } = this.props.branch;
    const currencies=[{value:"ZWL",label:"ZWL"},{value:"USD",label:"USD"}]

    return (
      <div className="container">

        <div className="card m-auto  border-primary shadow-lg rounded p-3">
          <h5 className="display-4 text-center">New Institution Type</h5>
          <p className="lead text-center">Add Institution Details</p>
          <hr />
          {errors.message && <div className="alert alert-danger text-center" role="alert">
            {errors.message}
          </div>}
          <form onSubmit={this.saveInstitution}>
            <div className="row">
              <div className="form-group col-sm-6">
                <label >Institution Name</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.institutionName })}
                  type="text"
                  name="institutionName"
                  id="institutionName"
                  value={this.state.institutionName}
                  onChange={this.onChange}
                  placeholder="Institution Name"
                  required

                />
                {errors.institutionName && (
                  <div className="invalid-feedback">
                    {errors.institutionName}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Institution Address</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.address })}
                  type="text"
                  name="address"
                  id="address"
                  value={this.state.address}
                  onChange={this.onChange}
                  placeholder="Institution Address"
                  required

                />
                {errors.address && (
                  <div className="invalid-feedback">
                    {errors.address}
                  </div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label >Institution Phone</label>
                <input
                  className={classnames("form-control",
                    { "is-invalid": errors.phone })}
                  type="text"
                  name="phone"
                  id="phone"
                  value={this.state.phone}
                  onChange={this.onChange}
                  placeholder="Institution Phone"
                  required

                />
                {errors.phone && (
                  <div className="invalid-feedback">
                    {errors.phone}
                  </div>
                )}
              </div>



              <div className="form-group col-sm-6">
                <label>Email</label>
                <input
                  className={classnames("form-control", {
                    "is-invalid": errors.email
                  })}
                  type="text"
                  name="email"
                  id="email"
                  value={this.state.email}
                  onChange={this.onChange}
                  placeholder="Email"
                  required
                />
                {errors.email && (
                  <div className="invalid-feedback">{errors.email}</div>
                )}
              </div>


              <div className="form-group col-sm-6">
                <label>Sector</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleSectorChange}
                  getOptionLabel={sector => `${sector.sectorName}`}
                  isOptionSelected={true}
                  getOptionValue={sector => `${sector}`}
                  options={sectors}
                  required
                />
              </div>
              <div className="form-group col-sm-6">
              <label>Branch</label>
              <Select
                className="react-selectcomponent"
                classNamePrefix="name-select"
                onChange={this.handleBranchChange}
                getOptionLabel={(branch) => `${branch.branchName}`}
                isOptionSelected={true}
                getOptionValue={(branch) => `${branch}`}
                required="true"
                options={branches}
              />
            </div>

              <div className="form-group col-sm-6">
                <label>TU Percentage</label>
                <input
                  className={classnames("form-control", {
                    "is-invalid": errors.tuPercentage
                  })}
                  type="number"
                  min="0"
                  name="tuPercentage"
                  id="tuPercentage"
                  value={this.state.tuPercentage}
                  onChange={this.onChange}
                  placeholder="TU Percentage"
                  
                />
                {errors.tuPercentage && (
                  <div className="invalid-feedback">{errors.tuPercentage}</div>
                )}
              </div>

              <div className="form-group col-sm-6">
                <label>Currency</label>
                <Select
                  className="react-selectcomponent"
                  classNamePrefix="name-select"
                  onChange={this.handleCurrencyChange}
                  options={currencies}
                  required
                />
              </div>

            </div>
            <div className="row">
              <div className="form-group col-sm-6">
                <button
                  type="submit"
                  className="btn btn-primary btn-block mt-4"

                >Save</button>
              </div>
              <div className="form-group col-sm-6">
                <button
                  type="button"
                  className="btn btn-secondary btn-block mt-4"
                  onClick={this.cancel}
                >Cancel</button>
              </div>
            </div>
          </form>
        </div>
      </div>

    );
  }
}

NewInstitution.propTypes = {
  createInstitution: PropTypes.func.isRequired,
  getBranches: PropTypes.func.isRequired,
  getSectors: PropTypes.func.isRequired,
  errors: PropTypes.object.isRequired
};

const mapStateToProps = state => ({
  sector: state.sector,
  branch: state.branch,
  errors: state.errors
});

export default connect(
  mapStateToProps,
  { createInstitution, getSectors, getBranches }
)(NewInstitution);
